.projects {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  background-color: rgb(255, 215, 86);
  margin-bottom: 180px;
  padding-top: 40px;
  padding-bottom: 120px;
  border-radius: 0%;
  margin-top: 240px;
  -webkit-box-shadow: 0px 0px 20px rgba(0, 0, 0, .4);
          box-shadow: 0px 0px 20px rgba(0, 0, 0, .4);
}

.projectImg {
  margin-top: -200px;
  border: solid 5px black;
  border-radius: 50%;
  padding: 40px;
  border-color: black;
  background-color: white;
  margin-bottom: 40px;
}

.projectImg img {
  height: 100px;
}

.project {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  cursor: pointer;
}

.centerPopup {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.inDev {
  margin-top: 10px;
  height: 240px;
  width: 500px;
  margin-bottom: -60px;
}

#inDev1 p {
  width: 490px;
}

#inDev2 {
  width: 100%;
  height: 100%;
}

.inDev img {
  width: 100%;
  height: 100%;
}

.snapshot {
  padding: 3px;
  position: relative;
  z-index: 2;
}

.snapshot img {
  width: 100%;
  height: 100%;
}

.makeNormal {
  -webkit-transition: opacity 200ms linear;
  -o-transition: opacity 200ms linear;
  transition: opacity 200ms linear;
  opacity: 1;
  -webkit-box-shadow: 0px 0px 10px rgba(0, 0, 0, 1.6);
          box-shadow: 0px 0px 10px rgba(0, 0, 0, 1.6);
}

.makeLight {
  -webkit-transition: opacity 200ms linear;
  -o-transition: opacity 200ms linear;
  transition: opacity 200ms linear;
  opacity: 1.35;
}


#ravanes2 , #camera2, #jammming2, #blocks2, #quiz2 {
  width: 525px;
  height: 240px;
}

.aboutPageHidden {
  background-color: rgba(255, 255, 255, .0);
/*  visibility: collapse; */
  /*margin-top difference between hidden and not*/
  -webkit-transition: opacity 200ms linear;
  -o-transition: opacity 200ms linear;
  transition: opacity 200ms linear;
  margin-top: -155px;
  padding-bottom: 28px;
  opacity: .0;
  padding-top: 1px;
  margin-bottom: 0px;
  margin-right: 0px;
  width: 100%;
  margin-top: -170px;
  text-align: center;
}

.aboutPage {
  background-color: rgba(255, 255, 255, .8);
  width: 100%;
  -webkit-transition: opacity 200ms linear;
  -o-transition: opacity 200ms linear;
  transition: opacity 200ms linear;
  /*translate over margin top from -1000px to -130px to
  show the detail tab about that project*/
  margin-top: -240px;
  padding-bottom: 28px;
  opacity: 1;
  margin-right: 0px;
  padding-top: 70px;
  margin-bottom: 0px;
  position: relative;
  text-align: center;
  z-index: 8;
}

.projects h2 {
  margin-top: -18px;
  font-family: Signika;
  font-weight: bold;
  opacity: 1;
  font-size: 26px;
}

.projects p {
  opacity: 1;
  font-size: 15px;
  font-family: poppins;
  width: 518px;
  text-align: center;
  padding: 10px 5px;
  margin-bottom: -10px;
  -webkit-box-shadow: 0px 0px 20px rgba(0, 0, 0, .4);
          box-shadow: 0px 0px 20px rgba(0, 0, 0, .4);
}

.aboutPage p {
  background-color: white;
  text-align: center;
}

/*Sizing*/

@media only screen and (max-width: 1100px) {
  .projects h2 {
    font-size: 22px;
  }

  #inDev1 p {
    width: 518px;
  }
  
  .projects p {
    font-size: 12px;
  }
  .projects {
    margin-top: 350px; 
  }

  .project {
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .inDev {
    width: 530px;
  }
}

@media only screen and (max-width: 600px) {
  #ravanes2 , #camera2, #jammming2, #blocks2, #quiz2 {
    width: 393.75px;
    height: 180px;
  }

  .snapshot {
    width: 400px;
    margin-bottom: 15px;
  }
  /*width: 316.6px;
  height: 144px; */

  .inDev {
    width: 395px;
    height: 180px;
  }

  .aboutPageHidden {
    width: 98.5%;
    margin-left: -1px;
  /*  visibility: collapse; */
    /*margin-top difference between hidden and not*/
    -webkit-transition: opacity 200ms linear;
    -o-transition: opacity 200ms linear;
    transition: opacity 200ms linear;
    margin-top: -180px;
    padding-bottom: 0px;
    opacity: .0;
    padding-top: 70px;
    margin-bottom: -1px;
    
    width: 100%;
    text-align: center;
  }

  .aboutPage {
    background-color: rgba(255, 255, 255, .8);
    width: 98.5%;
    -webkit-transition: opacity 200ms linear;
    -o-transition: opacity 200ms linear;
    transition: opacity 200ms linear;
    /*translate over margin top from -1000px to -130px to
    show the detail tab about that project*/
    margin-top: -180px;
    padding-bottom: 0px;
    opacity: 1;
    margin-left: -6px;
    padding-top: 70px;
    margin-bottom : -1px;
    position: relative;
    text-align: center;
    z-index: 8;
  }

  .projects p {
    opacity: 1;
    font-size: 11px;
    font-family: poppins;
    width: 384px;
    text-align: center;
    padding: 10px 5px;
    margin-bottom: -20px;
    -webkit-box-shadow: 0px 0px 20px rgba(0, 0, 0, .4);
            box-shadow: 0px 0px 20px rgba(0, 0, 0, .4);
    background-color: white;
  }

  .projects h2 {
    margin-bottom: 0px;
    margin-left: 0px;
  }

  #inDev1 {
    width: 395px;
    margin-right: -6px;
    
  }

  #inDev1 h2 {
    margin-top: -40px;
    margin-bottom: 30px;
  }

  #inDev1 p {
    width: 385px;
    margin-top: -14px;
  }

}



