.container {
  background-color: rgb(24, 26, 30);
  padding-bottom: 40px;
  padding-top: 40px;
  color: gray;
  -webkit-box-shadow: 0px 0px 20px rgba(0, 0, 0, .4);
          box-shadow: 0px 0px 20px rgba(0, 0, 0, .4);
}

.contactMe {
  width: 80%;
  margin-left: 10%;
}

.me {
  width: 30%;
  margin-bottom: 40px;
  color: white;
  -webkit-box-shadow: 0px -10px 20px rgba(255, 255, 255, .4);
          box-shadow: 0px -10px 20px rgba(255, 255, 255, .4);
  border-radius: 10%;
  padding: 10px;
  padding-left: 20px;
}

.me h1 {
  color:  rgb(255, 215, 86);
  font-family: Signika;
  font-size: 34px;
  margin-bottom: 40px;
}

.contact h2 {
  color:  rgb(255, 215, 86);
  font-family: Signika;
}

.contact p {
  font-family: Signika;
}

.tabs {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.socialMedia {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.socialMedia p {
  margin-top: -15px;
  padding: 10px;
  color: white;
}

.email p {
  padding: 15px;
  margin-top: -15px;
  color: white;
}

.email p:hover, .socialMedia p:hover {
  cursor: pointer;
  -webkit-transition: color 0.2s ease;
  -o-transition: color 0.2s ease;
  transition: color 0.2s ease;
  color: rgb(255, 215, 86);
}

.copyright p {
  font-size: 13px;
  font-family: poppins;
}

.coded {
  font-size: 12px;
  margin-bottom: 0px;
  float: right;
  margin-right: 80px;
}

/*sizing*/

@media only screen and (max-width: 1000px) {
  .me p, .me h1 {
    width: 300px;
  }

  .me {
    margin-bottom: 80px;
    width: 300px;
  }

  .copyright {
    margin-bottom: -40px;
    margin-left: -30px;
  }

  .coded {
    margin-right: 20px;
  }

  .email p {
    padding: 0px;
    padding-top: 20px;
  }

  .email {
    margin-left: -40px;
  }

  .socialMedia {
    margin-right: -40px;
    text-align: center;
  }

  .socialMedia p {
    padding: 0px;
    padding-top: 14px;
  }

  .tabs {
    margin-bottom: 60px;
  }

  .me h1 {
    font-size: 30px;
  }
}



