.header {
  background-color: white;
  width: 100%;
  height: 742px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  background-image: url("background.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.heading {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  margin-top: 0px;
  color: white;
}

.heading h1 {
  margin-top: 60px;
  color: black;
  margin-bottom: -5px;
  font-style: oblique;
  font-size: 46px;
  font-family: Signika;
}

.heading h2 {
  color: black;
  font-family: Amatic SC;
  margin-top: 10px;
  font-size: 32px;
}

.heading p {
  font-family: Signika;
  font-size: 30px;
  margin-top: 80px;
  color: black;
  width: 450px;
  height: 100px;
}

.picDiv {
  margin-top: 50px;
  height: 250px;
  border-radius: 80%;
  width: 200px;
  padding-bottom: 20px;

}

#profile {
  width: 200px;
  height: 270px;
}

#myCanvas {
  color: green;
  margin-top: 20px;
  width: 600px;
  height: 400px;
/*  border: 1px solid #d3d3d3;  */
}

#drawing  {
  height: 700px;
  margin-top: -230px;
  width: 1000px;
}

#drawing img {
  height: 100%;
}



@media only screen and (max-width: 980px) {
  #drawing  {
    height: 400px;
    margin-top: 60px;
    width: 700px;
  }
}

@media only screen and (max-width: 700px) {
  #drawing  {
    height: 400px;
    margin-top: 60px;
    width: 550px;
  }
}

@media only screen and (max-width: 550px) {
  #drawing  {
    height: 400px;
    margin-top: 60px;
    width: 400px;
  }
}

@media only screen and (max-width: 500px) {
  #drawing  {
    margin-top:-60px;
  }
  #profile {
    width: 200px;
    height: 270px;
  }

  .heading h1 {
    font-size: 36px;
    margin-bottom: 10px;
  }
  .heading h2 {
    font-size: 24px;
    margin-bottom: 50px;
  }
  .header {
    height: 717px;
  }
}



