 .lockStyle  {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding-bottom: -22px;
}
.normStyle {
  width: 100%;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 10;
  background-color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding-bottom: -22px;
  -webkit-box-shadow: 0px 5px 10px rgba(0, 0, 0, .4);
          box-shadow: 0px 5px 10px rgba(0, 0, 0, .4);
}

.navImg img {
  height: 60px;
}

.navTabs {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  text-align: center;
}

.navTabs a {
  font-family: poppins;
  margin: 0px;
  text-align: center;
  padding-top: 20.5px;
  padding-bottom: 20px;
  padding-left: 30px;
  padding-right: 30px;
}

.navTabs a:hover {
  background-color: rgba(255, 215, 86, .6);
}

.navTabs h3:hover {
  cursor: pointer;
}

.active {
  background-color: rgb(255, 215, 86);
  padding-top: 21px;
  padding-bottom: 24px;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: -20px;
  margin-right: -20px;
}

@media only screen and (max-width: 675px) {
  .navTabs h3 {
    font-size: 13px;
  }

  .navTabs a {
    padding-bottom: 14.2px;
  }
}

@media only screen and (max-width: 545px) {
  .navTabs h3 {
    font-weight: lighter;
  }
  .navTabs a {
    font-size: 15.5px;
    font-family: poppins;
    margin: 0px;
    text-align: center;
    padding-top: 14px;
    padding-bottom: 14.2px;
    padding-left: 11px;
    padding-right: 11px;
  }
}

@media only screen and (max-width: 440px) {
  .navTabs h3 {
    font-weight: lighter;
  }
  .navTabs a {
    font-size: 13.5px;
    font-family: poppins;
    margin: 0px;
    text-align: center;
    padding-top: 14px;
    padding-bottom: 14.2px;
    padding-left: 11px;
    padding-right: 11px;
  }
}



