.languages {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding-top: 20px;
  background-color: rgb(252, 252, 252);
  padding-top: 100px;
  padding-bottom: 40px;
}

.experience {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  margin-left: 30px;
  margin-right: 30px;
  width: 45%;
  border-left-style: solid;
  border-right-style: solid;
  border-left-width: 6px;
  border-right-width: 6px;
  border-radius: 20%;
  border-color: rgb(255, 215, 86);
  padding-left: 60px;
  padding-right: 60px;
  padding-top: 80px;
  padding-bottom: 80px;
}

#coder {
  height: 80px;
  margin-top: -50px;
}

.experience h2 {
  font-family: Signika;
}

.schools {
  display: -ms-flexbox;
  display: flex;
  width: 700px;
  -ms-flex-pack: justify;
      justify-content: space-between;
  text-align: center;
}
.topLogo {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 455px;
  margin-top: 10px;
  margin-bottom: -50px;
}

#programmer {
  height: 75px;
  margin-right: -10px;
}

#learning {
  height: 75px;
  margin-left: -10px;
}

.studied {
  border-left: 6px rgb(255, 215, 86) dotted;
  border-right: 6px rgb(255, 215, 86) dotted;
  padding: 20px 45px;
  margin-top: 40px;
}

.studied img {
  margin: -25px;
  height: 80px;
}

.schools h2 {
  width: 200px;
  font-family: Signika;
  font-size: 22px;
  margin-bottom: 50px;
}

.schools p {
  margin: 20px;
  font-family: Signika;
}



.pic {
  height: 150px;
  padding: 20px;
  margin-bottom: 50px;
