/*Sizing*/

@media only screen and (max-width: 1300px) {
    .experience {
      padding-left: 0px;
      padding-right: 0px;
    }

    #programmer {
      margin-bottom: -500px;
    }

    .schools {
      -ms-flex-direction: column;
          flex-direction: column;
      -ms-flex-align: center;
          align-items: center;
      margin-top: 80px;
    }

    .studied h2 {
      text-align: center;

    }

    .topLogo {
      -ms-flex-direction: column;
          flex-direction: column;
      width: 70px;
    }

    .topLogo img {
      margin-bottom: -430px;
      margin-top: -5px;
    }
}

@media only screen and (max-width: 1000px) {
  .languages {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
  }

  .logos {
    -ms-flex-direction: row;
        flex-direction: row;
    margin-top: 0px;
  }

  .pic {
    margin-bottom: 12px;
    margin-top: 12px;
    height: 80px;
  }

}

@media only screen and (max-width: 700px) {
  .studied {
    border: none;
  }

  .experience {
    width: 80%;
  }

  .schools {
    width: 100%; 
  }

  .logos {
    margin: 10px;
  }

  .logos img {
    padding: 10px;
    margin: 8px;
  }

}



